import React,{useEffect, useState} from 'react'
import { useParams } from 'react-router-dom'
import { fetchSales } from '../../core/api';
import { brandsReportColumns, getSize } from '../../utils/utils';
import { createBrandsDefaultDataSource,fillDataSource,filterByBrand, formatDataSource } from './Report.functions';
import { DatePicker, Table } from 'antd';
import moment from 'moment';
import { filterBySex } from '../Sales/SalesTable.functions';
import { brands, dateFormat, sizes } from '../../data';
const { RangePicker } = DatePicker;

const entitiesForReport = {
    brands:brands.slice(1),
    sizes
  }
export const Report = () => {
    const { sex } = useParams();
    const { reportBy } = useParams();
    const [dataSource, setDataSource] = useState(createBrandsDefaultDataSource(entitiesForReport[reportBy]));
    const [isLoading, setIsLoading] = useState(false);
    const [momentFrom, setMomentFrom] = useState("")
    const [momentTo, setMomentTo] = useState("")


    const updateDataSource = async (momentFrom, momentTo) => {  
        setIsLoading(true);
        let data = await fetchSales(undefined,momentFrom,momentTo);
         data = filterBySex(data, sex);
         let dataSource = createBrandsDefaultDataSource(entitiesForReport[reportBy]);
         if (reportBy === "brands") {
           brands.forEach((brand) => {
             const products = filterByBrand(data, brand);
             products.forEach((product) => {
               const idx = dataSource.findIndex((item) => item.name === brand);
               dataSource = fillDataSource(dataSource, product, idx);
             });
           });
         
         } else if (reportBy === "sizes") {
           data.forEach((product) => {
            const idx = dataSource.findIndex((item) => item.name === getSize(product.assortment.name));  
            dataSource = fillDataSource(dataSource,product,idx)
           })
         }
         setDataSource(formatDataSource(dataSource))
        setIsLoading(false);
      };
    useEffect(()=>{
    updateDataSource(momentFrom,momentTo)
    },[sex,momentFrom,momentTo,reportBy])

    return (
        <>
        <RangePicker
          onChange={(moments) => {
              setMomentFrom(moment(moments[0]._d).format(dateFormat))
              setMomentTo(moment(moments[1]._d).format(dateFormat))
          }}
          format={dateFormat}
          size="large"
        />
        <Table
        style={{width:"70vw"}}
        dataSource={dataSource}
        columns={brandsReportColumns}
        columnstock="3px"
        pagination={false}
        bordered={true}
        loading={isLoading}
         />
      </>
    )
}
