import React from 'react'
import {Route,NavLink} from 'react-router-dom'

import { Menu, Spin } from 'antd';
import { StockTable } from './components/Stock/StockTable';
import { SalesTable } from './components/Sales/SalesTable';
import { fetchCounterparty } from './core/api';
import { Report } from './components/Report/Report';
import { CommonReport } from './components/CommonReport/CommonReport';
import { CounterpartiesReport } from './components/CounterpartiesReport/CounterpartiesReport';

const { SubMenu } = Menu;

class App extends React.Component {
  state = {
    current: null,
    counterparties:null,
    loading:false
  };
  componentDidMount(){
    this.setState({ loading: true });
    fetchCounterparty().then((counterparties) => {
      this.setState({ counterparties, loading: false });
    });
  }

  handleClick = e => {
    this.setState({ current: e.key });
  };

  render() {
    const { current } = this.state;
  
    return (
      <>
        
          <Menu
            onClick={this.handleClick}
            selectedKeys={[current]}
            mode="horizontal"
            theme="dark"
          >
            <SubMenu key="boys" title="Мальчики">
              <Menu.Item key="boysStock">
                <NavLink to="/stock/boys">Oстатки </NavLink>
              </Menu.Item>
              <Menu.Item key="boysSales">
                <NavLink to="/sales/boys">Продажи </NavLink>
              </Menu.Item>
              <Menu.Item key="boysBrandReport">
                <NavLink to="/report/boys/brands">Отчет по брендам </NavLink>
              </Menu.Item>
              <Menu.Item key="boysSizesReport">
                <NavLink to="/report/boys/sizes">Отчет по размерам </NavLink>
              </Menu.Item>
            </SubMenu>

            <SubMenu key="girls" title="Девочки">
              <Menu.Item key="girlsStock">
                <NavLink to="/stock/girls">Oстатки </NavLink>
              </Menu.Item>
              <Menu.Item key="girlsSales">
                <NavLink to="/sales/girls">Продажи </NavLink>
              </Menu.Item>
              <Menu.Item key="girlsBrandReport">
                <NavLink to="/report/girls/brands">Отчет по брендам </NavLink>
              </Menu.Item>
              <Menu.Item key="girlsSizesReport">
                <NavLink to="/report/girls/sizes">Отчет по размерам </NavLink>
              </Menu.Item>
            </SubMenu>
            <SubMenu key="unisex" title="Унисекс">
              <Menu.Item key="unisexStock">
                <NavLink to="/stock/unisex">Oстатки</NavLink>
              </Menu.Item>
              <Menu.Item key="unisexSales">
                <NavLink to="/sales/unisex">Продажи</NavLink>
              </Menu.Item>
              <Menu.Item key="unisexBrandReport">
                <NavLink to="/report/unisex/brands">Отчет по брендам </NavLink>
              </Menu.Item>
              <Menu.Item key="unisexSizesReport">
                <NavLink to="/report/unisex/sizes">Отчет по размерам </NavLink>
              </Menu.Item>
            </SubMenu>
            <SubMenu key="common" title="Общее">
              <Menu.Item key="commonStock">
                <NavLink to="/stock/common">Oстатки</NavLink>
              </Menu.Item>
              <Menu.Item key="commonSales">
                <NavLink to="/sales/common">Продажи</NavLink>
              </Menu.Item>
              <Menu.Item key="commonBrandReport">
                <NavLink to="/report/common/brands">Отчет по брендам </NavLink>
              </Menu.Item>
              <Menu.Item key="commonSizesReport">
                <NavLink to="/report/common/sizes">Отчет по размерам </NavLink>
              </Menu.Item>
            </SubMenu>
          </Menu>
        
        <Route exact path="/stock/:sex" render={() => <StockTable />} />
        <Route path="/sales/:sex" render={() => <SalesTable counterparties={this.state.counterparties}/>} />
        <Route path="/report/:sex/:reportBy" render={() => <Report/>} />
        <CommonReport/>
        {this.state.counterparties && <CounterpartiesReport counterparties={this.state.counterparties}/>}
      </>
    );
  }
}
export default App
