import { Table } from 'antd';
import React, {useEffect,useRef,useState} from 'react'
import { createColumns } from '../../utils/utils';

////export const counterpartiesReportColumns = createColumns(["salesCount", "profit", "salesAvgCheck", "sellSum"],"counterpartiesReport")

const characteristics = ["salesCount", "profit", "salesAvgCheck", "sellSum"]
export function createDefaultDataSource(characteristics, counterpartiesNames){

    return characteristics.map((name,idx)=>{
    const values = counterpartiesNames.reduce((acc,counterparty) => {
        acc[counterparty] = 0
        return acc
    },{})
      return {
        key: idx,
        name,
        ...values,
        
      };
    })
  }
export const CounterpartiesReport = (props) => {
    const counterpartiesNames = [...props.counterparties.map((item) => item.counterparty.name),"total"]
const columns = createColumns(counterpartiesNames,"counterpartiesReport")
const [dataSource, setDataSource] = useState(createDefaultDataSource(characteristics,counterpartiesNames))
    

function updateDataSource(counterparties,counterpartiesNames){
        const newDataSource = createDefaultDataSource(characteristics,counterpartiesNames)
       counterparties.forEach((counterparty) => {
           for(let key in counterparty){
               
               if(newDataSource.some((item)=>item.name === key)){
                   
                   const oldObj = newDataSource.find((item) => item.name === key)
                   if (key !== "salesCount") {
                    
                     oldObj[counterparty.counterparty.name] = +(
                       parseInt(counterparty[key]) / 100
                     ).toFixed(0);

                   } else {
                    oldObj[counterparty.counterparty.name] = counterparty[key]

                   }

                    
               }
           }
       })
       newDataSource.forEach((item) => {
    
    item.total = counterpartiesNames.reduce((acc,name) => {
        acc += item[name]
        return acc
    },0 )
})
setDataSource(newDataSource)

    }
    useEffect(()=>{
        updateDataSource(props.counterparties,counterpartiesNames)
    },[props.counterparties])


    return (
        <>
        {/* <RangePicker
        style={{"marginTop":"2rem"}}
          onChange={(moments) => {
            setMomentFrom(moment(moments[0]._d).format(dateFormat))
            setMomentTo(moment(moments[1]._d).format(dateFormat));
          }}
          format={dateFormat}
          size="large"
        />*/
        <Table
        style={{width:"70vw"}}
        dataSource={dataSource}
        columns={columns}
        size="small"
        columnstock="3px"
        pagination={false}
        bordered={true}
        loading={props.loading}
        
         /> }
      </>
    )
}