
export function createBrandsDefaultDataSource(items){
    const itemsCopy = [...items]
    return itemsCopy.map((name,idx)=>{
        
        return {
          key: idx,
          name,
          sum:0,
          profit:0,
          sellCostSum:0,
          profitability:0
        };
      })
  }


 export  const formatDataSource = (dataSource) => {
    return dataSource.map((name) => {
      const profitability = +((name.profit * 100) / name.sellCostSum).toFixed(
        2
      );
      
      return {
        ...name,
        sum: (name.sum / 100).toFixed(2),
        profit: (name.profit / 100).toFixed(2),
        profitability: !!profitability ? profitability + "%" : 0,
      };
    });
  }

 export const fillDataSource = (dataSource, product, idx) => {
   const data = [...dataSource];
   if (idx > -1) {
     data[idx] = {
       ...data[idx],
       sum: (data[idx].sum += +product.sellSum),
       profit: (data[idx].profit += +product.profit),
       sellCostSum: (data[idx].sellCostSum += +product.sellCostSum),
     };
   }
   return data;
 };

  export const filterByBrand = (products, brand) => {
    const res =  products.filter((product) => {
       return product.assortment.name.indexOf(brand) !== -1;
     });
     return res
   }
